.footer {
    padding: 32px 0;
    border-radius: 64px 64px 0 0;
    background-color: #0b131c;
    position: relative;
    @include small-tablet{
      padding: 20px 0;
      border-radius: 44px 44px 0 0;
    }
		// .footer__content

		&__content {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      gap: 20px;
      @include small-tablet{
        flex-direction: column;
        gap: 0;
      }
		}

		// .footer__contacts

		&__contacts {
      display: flex;
      flex-direction: column;
      gap: 32px;
      @include mobile{
        gap: 12px;
        margin-bottom: 48px;
      }
		}

		// .footer__logo-link

		&__logo-link {
		}

		// .footer__logo

		&__logo {
		}

		// .footer__text

		&__text {
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: white;
      @include mobile{
        font-size: 14px;
        line-height: 18px;
      }
		}

		// .footer__social

		&__social {
      display: flex;
      align-items: center;
      gap: 8px;
      @include mobile{
        margin-top: 12px;
      }
		}

		// .footer__social-list

		&__social-list {
      display: flex;
      align-items: center;
      gap: 12px;
		}

		// .footer__social-item

		&__social-item {
		}

		// .footer__social-link

		&__social-link {
		}

		// .footer__email

		&__email {
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: white;
      @include hover{
        color: var(--accent-color);
      }
      @include mobile{
        font-size: 14px;
      }
		}

		// .footer__list

		&__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 24px;
      @include mobile{
        grid-template-columns: repeat(2, 1fr);
        gap: 24px 48px;
        margin-bottom: 24px;
      }
		}

		// .footer__item

		&__item {
		}

		// .footer__link

		&__link {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: #fff;
      @include hover{
        color: var(--accent-color);
      }
		}

		// .footer__coppyright

		&__coppyright {
      align-self: stretch;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
		}

    &__coppyright-text{
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      color: #7c7c7c;
    }

		// .footer__upper

		&__upper {
      text-align: right;
      @include mobile{
        position: absolute;
        top: 20px;
        right: 20px;
      }
		}
}
