.hero {
    padding-top: 170px;
    padding-bottom: 35px;
    border-radius: 0 0 64px 64px;
    background-image: url(../img/hero-bg.jpg);
    background-size: cover;
    position: relative;
    z-index: 2;
    @include big-tablet{
      border-radius: 0 0 44px 44px;
      overflow: hidden;
    }
    @include mobile{
      background-position: 600px;
    }
		// .hero__title

		&__title {
      text-align: center;
      color: white;
      font-weight: 300;
      font-size: 52px;
      line-height: 56px;
      margin: 0;
      margin-bottom: 20px;
      span:not(.hero__title-bottom){
        background: linear-gradient(45deg, #fff 0%, #2e64e7 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      @include tablet{
        font-size: 36px;
        line-height: 40px;
        max-width: 400px;
        margin: 0 auto;
        br{
          display: none;
        }
      }
		}

		// .hero__title-bottom

		&__title-bottom {
      display: block;
      margin-top: 13px;
      font-weight: 300;
      font-size: 18px;
      line-height: 20px;
      @include mobile{
        font-size: 14px;
      }
		}

		// .hero__content

		&__content {
      display: grid;
      grid-template-columns: minmax(100px, 275px) 1fr minmax(100px, 275px);
      gap: 48px;
      @include big-tablet{
        grid-template-columns: 1fr;
      }
		}

		// .hero__left

		&__left {
      display: flex;
      flex-direction: column;
      gap: 48px;
      @include big-tablet{
        display: none;
      }
      .hero__block-img{
        min-width: 238px;
        @include big-tablet{
          min-width: unset;
        }
      }
		}

		// .hero__block

		&__block {
      position: relative;
      border-radius: 36px;
      backdrop-filter: blur(44px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.05) 100%);
      padding: 20px;
      padding-right: 70px;
      position: relative;
      z-index: 2;

      @include big-tablet{
        min-width: 275px;
        height: 215px;
      }
		}

		// .hero__block-name

		&__block-name {
      font-weight: 300;
      font-size: 18px;

      line-height: 20px;
      color: #fff;
      display: block;
      margin-bottom: 12px;
		}

		// .hero__block-text

		&__block-text {
      display: block;
      font-weight: 300;
      font-size: 12px;

      line-height: 16px;
      color: #fff;
      margin-bottom: 18px;

      @include big-tablet {
        font-size: 14px;
      }
		}

		// .hero__block-arrow

		&__block-arrow {
      position: absolute;
      top: 20px;
      right: 20px;
		}

		// .hero__block-img

		&__block-img {
      margin-left: 70px;
      &-reliability{
        margin-left: 35px;
      }
      @include big-tablet{
        max-height: 85px;
      }
		}

		// .hero__block-imgs

		&__block-imgs {
      display: flex;
      margin-bottom: 12px;
		}

		// .hero__profitability

		&__profitability {
      margin-top: 65px;
		}

		// .hero__calculator

		&__calculator {
      backdrop-filter: blur(44px);
      background: black;
      border-radius: 20px;
      height: 376px;
      overflow: hidden;
      margin-bottom: 24px;
      position: relative;
      padding: 5px;
      &::after{
        display: block;
        content: "";
        position: absolute;
        inset: 0;
        backdrop-filter: blur(36.485836029052734px);
        background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
      }
      @include big-tablet{
        height: 488px;
        margin-bottom: 35px;
      }
      @include mobile{
        max-width: 305px;
        margin: 0 auto;
        margin-bottom: 35px;
        border-radius: 12px;
        position: relative;
        z-index: 1;
        padding: 0;
      }
		}

		// .hero__calculator-head

		&__calculator-head {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      padding: 16px 12px;
      backdrop-filter: blur(44px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
      border-radius: 24px;
      position: relative;
      z-index: 2;
      margin: 2px 5px;
      @include mobile{
        flex-direction: column;
        gap: 10px;
        border-radius: 12px;
        padding: 12px;
        margin: 0;
      }
		}

		// .hero__calculator-head-text

		&__calculator-head-text {
      display: flex;
      align-items: center;
      gap: 12px;

      span{
        font-weight: 500;
        font-size: 18px;

        line-height: 20px;
        color: #fff;
        @include mobile{
          font-weight: 300;
          br{
            display: none;
          }
        }
      }
		}

		// .hero__calculator-inputs

		&__calculator-inputs {
      display: grid;
      grid-template-columns: 145px 140px;
      gap: 24px;
      @include mobile{
        grid-template-columns: 1fr 1fr;
      }
		}

		// .hero__calculator-input-wrapper

		&__calculator-input-wrapper {
      display: flex;
      flex-direction: column;
      gap: 4px;
		}

		// .hero__calculator-input-text

		&__calculator-input-text {
      font-weight: 300;
      font-size: 12px;

      line-height: 16px;
      color: #fff;
      @include mobile{
        font-size: 11px;
      }
		}

		// .hero__calculator-label

		&__calculator-label {
      display: flex;
      align-items: center;
      background-color: #000;
      padding: 6px 8px;
      border-radius: 8px;
      gap: 4px;

      svg{
        flex: none;
      }
      @include mobile{
        max-width: 116px;
      }
		}

		// .hero__calculator-input

		&__calculator-input {
      background-color: transparent;
      font-weight: 300;
      font-size: 12px;
      line-height: normal;
      color: #979799;
      padding: 0;
      width: 100%;
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
		}

		// .hero__calculator-year

		&__calculator-year {
      font-weight: 300;
      font-size: 12px;

      line-height: normal;
      color: #979799;

      transform: translateY(1px);
		}

		// .hero__profitability-link

		&__profitability-link {
      padding: 20px;
      backdrop-filter: blur(6px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.05) 100%);
      border-radius: 36px;
      display: inline-block;
      position: relative;
      z-index: 1;

      @include hover{
        .hero__profitability-link-text{
          background-color: #2453c0;
        }
      }

      @include big-tablet{
        margin-bottom: 48px;
        width: 100%;
      }
		}

		// .hero__profitability-link-text

		&__profitability-link-text {
      padding: 23px 54px;
      background-color: var(--accent-color);
      font-weight: 300;
      font-size: 18px;

      line-height: 20px;
      color: #fff;
      border-radius: 36px;
      display: inline-block;
      transition: all .2s ease-in;
      @include big-tablet{
        width: 100%;
        font-size: 14px;
        padding: 15px;
      }
		}

		// .hero__right

		&__right {
      display: flex;
      flex-direction: column;
      align-self: flex-end;
      gap: 48px;
      margin-bottom: 15px;
      @include big-tablet {
        display: none;
      }
       .hero__block-img{
        min-width: 144px;
        margin-left: 50px;
        @include big-tablet{
          min-width: unset;
        }
      }
		}

    &__hand{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-61%);
      pointer-events: none;
      @include big-tablet{
        display: none;
      }
      @include mobile{
        display: block;
        bottom: 176px;
        transform: translateX(-36%);
        max-width: unset;

    }

    }

    &__blocks-mob {
      display: none;
      @include big-tablet {
        display: flex;
        gap: 12px;
        overflow: auto;
        margin-right: -20px;
        &::-webkit-scrollbar {
          display: none;
        }
      }
    }
}
