        align-self: flex-end;
      }
      &:nth-child(3){
        min-height: 280px;
      }
      &:nth-child(4){
        min-height: 340px;
      }

      @include mobile{
        &:nth-child(n){
          min-height: 252px;
        }
      }

      &--accent{
        box-shadow: 0 18px 88px -4px rgba(255, 255, 255, 0.14), 0 8px 28px -17px rgba(255, 255, 255, 0.12);
        background: #2e64e7;
      }
		}

