		&__item-title {
      font-size: 18px;
      color: white;
      margin-bottom: 12px;
      display: block;
		}

		// .about__item-text

		&__item-text {
      font-size: 14px;
      color: white;
      position: relative;
      z-index: 2;
      line-height: 20px;
      font-weight: 300;
		}

		// .about__item-arrow

		&__item-arrow {
      position: absolute;
      top: 20px;
      right: 20px;
		}

		// .about__item-bg

		&__item-bg {
      position: absolute;
      bottom: -20px;
      right: 0;
      @include mobile{
        max-width: 240px;
      }
		}
}

.work-steps {
    padding: 150px 0;
    border-radius: 64px;
    background-color: #0b131c;
    position: relative;
    z-index: 2;
    background-image: url(../img/work-steps-bg.jpg);
    background-size: cover;
    @include tablet{
      padding: 40px 0;
      border-radius: 44px;
    }

    &__bg{

    }

		// .work-steps__head

		&__head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 64px;
      gap: 20px;
      position: relative;
      z-index: 2;
      @include tablet{
        flex-direction: column;
        align-items: flex-start;
        gap: 24px;
        margin-bottom: 48px;
      }
		}

    &__wrapper{
      position: relative;
      z-index: 2;
      @include small-tablet{
        display: flex;
        gap: 24px;
      }
    }

		// .work-steps__title

		&__title {
      font-weight: 300;
      font-size: 52px;
      color: white;
      margin: 0;
      line-height: 56px;

      span{
        background: linear-gradient(45deg, #fff 0%, #a0b8f7 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      @include tablet{
        font-size: 36px;
        line-height: 40px;
      }
		}

		// .work-steps__text

		&__text {
      font-size: 14px;
      color: white;
      max-width: 356px;
      @include tablet{
        max-width: unset;
      }
		}

		// .work-steps__application

		&__application {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      padding: 12px 24px;
      border-radius: 24px;
      background-color: var(--accent-color);
      color: white;
      line-height: normal;
      @include hover{
        background-color: #2453c0;
      }
      @include tablet{
        width: 100%;
      }
		}

		// .work-steps__steps

		&__steps {
      margin-bottom: 64px;
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      @include big-tablet{
        gap: 5px;
      }
      @include small-tablet{
        grid-template-columns: unset;
        grid-template-rows: repeat(6, 1fr);
        margin: 0;
        gap: 18px;
      }
      svg{
        grid-column: 6 span;
        @include small-tablet{
          grid-column: unset;
          grid-row: 6 span;
        }
      }
		}

    &__steps-svg{
      transition: all .4s ease-in;
      @include small-tablet{
        display: none;
      }
      &--mob{
        display: none;
        min-width: 150px;
        @include small-tablet{
          display: block;
          max-width: 200px;
        }
      }
    }

		// .work-steps__info

		&__info {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      @include big-tablet{
        gap: 5px;
      }
      @include tablet{
        grid-template-columns: repeat(6, 1fr);
      }
      @include small-tablet{
        grid-template-columns: unset;
        grid-template-rows: repeat(6, 1fr);
        gap: 10px;
      }
		}

		// .work-steps__info-item

		&__info-item {
      display: flex;
      flex-direction: column;
      gap: 12px;
      max-width: 180px;
      transition: all .2s ease-in;
      position: relative;
      &::after{
        display: block;
        content: "";
        position: absolute;
        width: 35px;
        height: 35px;
        background-color: #2e64e7;
        filter: blur(40.999996185302734px);
        transition: all .2s;
        top: -160px;
        left: 75px;
        opacity: 0;
        @include mobile{
          display: none;
        }
      }
      @include small-tablet{
        max-width: unset;
        justify-content: center;
        gap: 6px;
      }
      &--active{
        padding-left: 12px;
        &:after{
          opacity: 1;
        }

        .work-steps__info-title{
         color: white;
         &::after{
          opacity: 1;
         }
        }
        .work-steps__info-text{
          color: white;
        }
      }
		}

		// .work-steps__info-title

		&__info-title {
      font-weight: 500;
      font-size: 20px;
      color: #474d5a;
      line-height: 1.2;
      position: relative;
      transition: all .2s ease-in;
      @include tablet{
        font-size: 18px;
        br{
          display: none;
        }
      }
      &::after{
        display: block;
        content: "";
        position: absolute;
        left: -12px;
        bottom: 0;
        top: 0;
        width: 2px;
        background-color: #2e64e7;
        opacity: 0;
        transition: all .2s ease-in;
      }
		}

		// .work-steps__info-text

		&__info-text {
      font-weight: 300;
      font-size: 14px;
      color: #474d5a;
      line-height: 22px;
      transition: all .2s ease-in;
      @include tablet{
        line-height: 18px;
        br{
          display: none;
        }
      }
		}
}
