.accordion {
    display: flex;
    flex-direction: column;
    gap: 8px;
      position: relative;
      z-index: 2;
		// .accordion__item

		&__item {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      gap: 12px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 36px;
      padding: 20px;
      transition: all .2s ease-in;
      overflow: hidden;
      @include hover{
        background-color: rgb(46, 100, 231);
      }
      @include tablet{
        justify-content: unset;
      }
      &--active{
        background-color: rgb(46, 100, 231);
        box-shadow: 0 18px 88px -4px rgba(255, 255, 255, 0.14), 0 8px 28px -6px rgba(255, 255, 255, 0.12);
        height: 100%;
        .accordion__item-arrow{
          transform: rotate(45deg);
        }
        .accordion__item-text{
          opacity: 1;
          transition: all .6s ease-in;
        }
        .accordion__item-img{
          opacity: 1;
          transition: all .6s ease-in;
        }
      }

      svg{
        flex: none;

      }
		}

    &__item-inner{

    }

    &__item-content{
      display: flex;
      align-items: flex-start;
      gap: 12px;
      pointer-events: none;
      @include tablet{
        flex-direction: column;
      }
    }

		// .accordion__item-info

		&__item-info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      text-align: left;
      gap: 12px;
      margin-top: 15px;
      max-width: 470px;
      pointer-events: none;
      @include tablet{
        max-width: 100%;
      }
		}

		// .accordion__item-title

		&__item-title {
      font-weight: 300;
      font-size: 18px;
      color: white;
		}

		// .accordion__item-text

		&__item-text {
      margin: 0;
      font-weight: 300;
      font-size: 14px;
      color: white;
      line-height: 20px;
      opacity: 0;
		}

		// .accordion__item-img

		&__item-img {
      opacity: 0;

      pointer-events: none;
		}

    &__item-arrow{
      margin-left: auto;

    }
}

