.header{
  padding: 42px 0;
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 13;
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__link{
    font-size: 12px;
    color: black;
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 12px 42px;
    background-color: white;
    border-radius: 24px;
    line-height: normal;
    @include hover{
      background-color: rgb(206, 205, 205);
    }
    @include big-tablet{
      display: none;
    }
  }

  &__menu{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }

  &__logo {
    position: relative;
    z-index: 13;
}
}


.menu{
  @include big-tablet{
  position: fixed;
  background-color: #000a17;
  bottom: 0;
  top: 0;
  right: -100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 40px;
  transition: right .2s ease-in;
  z-index: 12;
  padding: 0 20px;
  padding-top: 150px;
  &--active{
    right: 0;
  }
  }
  &__list {
    display: flex;
    column-gap: 24px;
    @include big-tablet{
      gap: 50px;
      row-gap: 24px;
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  &__link{
    font-weight: 400;
    font-size: 12px;
    color: white;
    @include hover{
      color: var(--accent-color)
    }
  }

    &__item-btn{
    grid-column: 2 span;
  }

  &__link-btn{
    font-size: 12px;
    color: black;
    align-items: center;
    gap: 4px;
    padding: 12px 42px;
    background-color: white;
    border-radius: 24px;
    line-height: normal;
    color: black;
    display: none;
    justify-content: center;
    @include big-tablet{
      display: flex;
      grid-column: 2 span;
    }
  }

}

.burger-wrapper{
  background-color: white;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  display: none;
  @include big-tablet{
    display: flex;
    z-index: 13;
  }
}

.burger{
  @include burger;
  display: none;
  @include big-tablet{
  display: block;
    z-index: 13;
 }
}
